.main-div{
    background: #262730;
    height: 391px;
}

#dragger{
    background: #32333D;
    border: 1px solid transparent !important;
    border: none !important;
}

#CloudUploadOutlined, .similarity{
    color: #FFFFFF;
}

#CloudUploadOutlined{
    font-size: 56px;
}

.grey-text{
    color: #888C9C;
}

.ant-slider-handle::after{
    background-color: #FF4B4B !important;
    border-color: #FF4B4B!important;
    box-shadow: 0 0 0 2px #FF4B4B !important;
}
#aaaa{
    border-radius: 5px;
    background-color:#32333D ;
}

.ant-modal-content {
    background-color: #32333D !important;
    color: #FFFFFF !important;
    padding: 60px 0px !important;
}

.dashboardParentDiv{
    background:#262730;
     color:#fff;
  
}
.groupNameHeading{
   font-size: 1.3rem;
   font-weight: 400; 
 padding-top: 5px;
}

.NameGroup{
    display: flex ; 
    justify-content: space-between;
}
.CSVButton{
    border: 1px solid #FF4B4B!important;
    color: #FF4B4B !important;
    width: 180px;
    background-color: #32333D;
}
.CSVButton :hover, .CSVButton :active{
    /* border: 1px solid #FF4B4B!important; */
    color: #FF4B4B !important;
}
.summarySection{
    border-radius: 5px;
    background-color: #32333D !important;
    color: #FFFFFF !important;
    font-size: 13px;
    margin-top: 20px;
    padding: 5px 0px;
}
.summary{ 
    margin:10px 20px ;
    color: #ffffff;
}
.summaryParagraph{
    color:#888C9C ;
    margin:10px 20px ;
}

.topicCard{
    background-color: #32333D !important;
    border-radius: 5px;
    
}
.topics, .similerText{ 
    text-align: justify;
    margin: 20px 20px 0px 20px;
    line-height: 2rem;
    letter-spacing: 2px;
    color: #ffffff;
}
.topicParagraph{
    color:#888C9C ;
    margin:15px 20px ;
    font-size: 16px;
}
.similerTextParagraph{
    color:#888C9C ;
    margin:15px 20px ;
    font-size: 16px;
}
.previousButton{
    border: 1px solid #32333D !important;
    border-radius: 5px;
    margin: 0px 10px;
    width: 150px;
    height: 50px;
    background-color: #32333D !important;
    color: #888C9C !important;
}
.previousButton :focus{
    border: 1px solid #32333D !important;
 }
.dropdownButton{
    border: 1px solid #32333D !important;
    border-radius: 5px;
    width: 150px;
    font-size: 16px;
    height: 50px;
    margin: 0px 10px;
    background-color: #32333D !important;
    color: #ffffff !important;
}
.NextButton{
    border: 1px solid #FF4B4B !important;
    border-radius: 5px;
    width: 150px;
    height: 50px;
    margin: 0px 10px;
    background-color: #FF4B4B !important;
    color: #ffffff !important;
}

 button:focus {
    outline: 1px #02020200 ;
    outline: none !important;
}
.ant-space {
    gap: 65px !important;
}
.customDropdown .ant-dropdown-menu {
    background-color: #32333D !important;
